import {
  Component,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
} from '@angular/core';
import { Router } from '@angular/router';
import { ReservationsService } from '@composite/widget/reservations/reservations.service';
import { Subscriptions } from '@core/decorators/subscriptions.decorator';
import { DictionariesService } from '@shared/dictionaries/dictionaries.service';
import { MeasurementsService } from '@shared/dictionaries/services/measurements.service';
import { forkJoin, Observable, of, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-widget-reservations',
  templateUrl: './reservations.component.html',
  styleUrls: ['./reservations.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [ReservationsService],
})
export class ReservationsComponent {
  reservations: any[] = [];
  current: any;
  dateFormat: string;
  timeFormat: string;
  dateTimeFormat: string;
  descriptionList: any;
  tags: Observable<string[]>;
  statuses: any;
  @Subscriptions() sub: Subscription;

  constructor(
    private _service: ReservationsService,
    private _cdr: ChangeDetectorRef,
    private _measurementsService: MeasurementsService,
    private _dictService: DictionariesService,
    private _router: Router
  ) {
    this.sub = _service
      .getWorkplaceReservations()
      .subscribe(({ reservations, statuses }) => {
        this.reservations = reservations.splice(0, 5);
        this.chooseActive(0);
        this.statuses = { map: statuses };
        this._cdr.markForCheck();
      });
    this.dateFormat = this._measurementsService.getMeasurementByName('date');
    this.timeFormat =
      this._measurementsService.getMeasurementByName('shortTime');
    this.dateTimeFormat =
      this._measurementsService.getMeasurementByName('dateTime');
  }

  chooseActive(index: number): void {
    this.current = this.reservations[index];
    this.setTags(this.current?.workplace?.tags || []);
  }

  setTags(tags: number[]): void {
    if (tags?.length) {
      this.tags = forkJoin([
        ...tags.map((tagId) =>
          this._dictService.getDictionaryItemByKey<{ name: string }>(
            'tags',
            tagId,
            ''
          )
        ),
      ]).pipe(map((_tags: { name: string }[]) => _tags.map((tag) => tag.name)));
    } else {
      this.tags = of([]);
    }
  }

  openReservation(id: number): void {
    this._router.navigate(['/', 'reservations', 'workplace', 'view', id]);
  }
}
