import { DatePipe } from '@angular/common';
import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
} from '@angular/core';
import { Router } from '@angular/router';
import { TranslatePipe } from '@ngx-translate/core';
import { IListRows } from '@src/app/base/list/models/list.model';
import { first } from 'rxjs/operators';
import { CalendarnewService } from './calendarnew.service';

@Component({
  selector: 'app-widget-calendarnew',
  templateUrl: './calendarnew.component.html',
  styleUrls: ['./calendarnew.component.less'],
  providers: [CalendarnewService],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CalendarnewComponent implements OnInit {
  data: IListRows = [];
  minDate = new Date();
  isLoading = true;

  constructor(
    private $calendarService: CalendarnewService,
    private cdr: ChangeDetectorRef,
    private router: Router,
    private _datePipe: DatePipe,
    private _translatePipe: TranslatePipe
  ) {}

  ngOnInit(): void {
    this.minDate.setDate(this.minDate.getDate() - 1);
    let dateFrom = new Date();
    dateFrom.setDate(0);
    dateFrom.setHours(0);
    dateFrom.setMinutes(0);
    let dateTo = new Date(dateFrom);
    dateTo.setMonth(dateTo.getMonth() + 1);
    this.updateDate(dateFrom, dateTo);
  }

  updateDate(dateFrom: Date, dateTo: Date) {
    // if (dateFrom < new Date()) {
    //   dateFrom = new Date();
    // }
    this.isLoading = true;
    dateFrom.setMonth(dateFrom.getMonth() - 1);
    this.$calendarService
      .getReservations$(dateFrom, dateTo)
      .pipe(first())
      .subscribe((result) => {
        this.data = [];
        const now = new Date();

        if (Array.isArray(result.roomAppointments)) {
          this.data.push(
            ...result.roomAppointments.map((roomAppointment) => {
              return {
                title: `${this._translatePipe.transform(
                  `Meeting room reservation`
                )}: ${roomAppointment.appointmentLocationString}`,
                start: new Date(roomAppointment.appointmentDateFrom),
                end: new Date(roomAppointment.appointmentDateTo),
                cssClass: 'calendar-busy-area-room',
                meta: {
                  incrementsBadgeTotal: false,
                },
                link: roomAppointment.appointmentId
                  ? '/reservations/appointment/view/' +
                    encodeURIComponent(roomAppointment.appointmentId)
                  : null,
              };
            })
          );
        }
        if (Array.isArray(result.parkinglots)) {
          this.data.push(
            ...result.parkinglots
              .filter(
                (parkinglot) =>
                  new Date(parkinglot.parkinglotsReservation.dateTimeTo) >
                    now || parkinglot.parkinglotsReservation.status === 'CLOSED'
              )
              .map((parkinglot) => {
                return {
                  title: `${this._translatePipe.transform(
                    `Parking space reservation`
                  )}: ${parkinglot.parkingLot.name}`,
                  start: new Date(
                    parkinglot.parkinglotsReservation.dateTimeFrom
                  ),
                  end: new Date(parkinglot.parkinglotsReservation.dateTimeTo),
                  cssClass: 'calendar-busy-area-parking',
                  meta: {
                    incrementsBadgeTotal: false,
                  },
                  link:
                    '/reservations/parking/view/' +
                    encodeURIComponent(parkinglot.parkinglotsReservation.id),
                };
              })
          );
        }
        if (Array.isArray(result.workplacereservetions)) {
          this.data.push(
            ...result.workplacereservetions
              .filter(
                (workplacereservetion) =>
                  new Date(workplacereservetion.reservation.dateTimeTo) > now ||
                  workplacereservetion.reservation.status === 'CLOSED'
              )
              .map((workplacereservetion) => {
                return {
                  title: `${this._translatePipe.transform(
                    `Work space reservation`
                  )}: ${
                    workplacereservetion.workplace
                      ? workplacereservetion.workplace.name
                      : ''
                  }`,
                  start: new Date(
                    workplacereservetion.reservation.dateTimeFrom
                  ),
                  end: new Date(workplacereservetion.reservation.dateTimeTo),
                  cssClass: 'calendar-busy-area-workplace',
                  meta: {
                    incrementsBadgeTotal: false,
                  },
                  link:
                    '/reservations/workplace/view/' +
                    encodeURIComponent(workplacereservetion.reservation.id),
                };
              })
          );
        }
        this.isLoading = false;
        this.cdr.detectChanges();
      });
  }

  eventHandler(ev) {
    if (ev.link) {
      this.router.navigate([ev.link]);
    }
  }
  monthSelectHandler(ev) {
    this.updateDate(new Date(ev[0]), new Date(ev[1]));
  }
}
