import { Injectable } from '@angular/core';
import { UserService } from '@core/services/user.service';
import { DictionariesService } from '@shared/dictionaries/dictionaries.service';
import {
  IWorkplace,
  IWorkplaceReservation,
} from '@shared/http/models/database.model';
import { ReservationsApiService } from '@shared/http/services/reservations-api.service';
import { forkJoin, Observable } from 'rxjs';
import { map, shareReplay, switchMap } from 'rxjs/operators';

@Injectable()
export class ReservationsService {
  constructor(
    private $reservationsApi: ReservationsApiService,
    private $user: UserService,
    private $dictionaries: DictionariesService
  ) {}
  getWorkplaceReservations(): Observable<any> {
    return forkJoin({
      reservations: this.$reservationsApi.getWorkplaceReservations({
        labelId: this.$user.info.id,
      }),
      workplaces: this.$dictionaries.getDictionary<IWorkplace[]>('workplaces'),
      statuses: this.$dictionaries.getDictionary('statuses'),
    }).pipe(
      map(({ reservations, workplaces, statuses }) => {
        return {
          reservations: reservations
            ? reservations.map((_r) => ({
                reservation: _r,
                workplace: workplaces.find((_w) => _w.id === _r.workplaceId),
              }))
            : [],
          statuses: statuses[0].statuses.reduce((acc, status) => {
            acc[status.code] = {
              value: status.name,
              color: status.color,
            };
            return acc;
          }, {}),
        };
      })
    );
  }
}
